// src/slices/authSlice.js
import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import {
  loginRequest,
  registerRequest,
  getStatusRequest,
  getCurrentUserRequest,
} from "../apiRequests";

const token = localStorage.getItem("authToken");

//   
export const loginUser = createAsyncThunk(
  "auth/loginUser",
  async ({ username, password }, { rejectWithValue }) => {
    try {
      const token = await loginRequest(username, password); //  
      const user = await getCurrentUserRequest(); //     
      return { token, user }; // user  id username 
    } catch (error) {
      return rejectWithValue(error.response?.data || "Failed to login");
    }
  }
);

//   
export const registerUser = createAsyncThunk(
  "auth/registerUser",
  async (
    { username, password, email, quizQuestion, userAnswer },
    { rejectWithValue }
  ) => {
    try {
      return await registerRequest({
        username,
        password,
        email,
        quizQuestion,
        userAnswer,
      });
    } catch (error) {
      return rejectWithValue(error.response?.data || "Failed to register");
    }
  }
);

//     
export const getCurrentUser = createAsyncThunk(
  "auth/getCurrentUser",
  async (_, { rejectWithValue }) => {
    try {
      const user = await getCurrentUserRequest(); //   
      return user; // user  id username 
    } catch (error) {
      return rejectWithValue(
        error.response?.data || "Failed to fetch current user"
      );
    }
  }
);

//    
export const getStatus = createAsyncThunk(
  "auth/getStatus",
  async (_, { rejectWithValue }) => {
    try {
      return await getStatusRequest(); //    
    } catch (error) {
      return rejectWithValue(error.response?.data || "Failed to fetch status");
    }
  }
);

const authSlice = createSlice({
  name: "auth",
  initialState: {
    isLoggedIn: !!token,
    token: token,
    user: { id: null, username: null },
    error: null,
    loginStatus: "idle",
    registerStatus: "idle",
    userStatus: "idle",
    statusState: "idle",
    statusData: null,
  },
  reducers: {
    logout(state) {
      state.isLoggedIn = false;
      state.token = null;
      state.user = { id: null, username: null };
      state.loginStatus = "idle";
      localStorage.removeItem("authToken");
    },
  },
  extraReducers: (builder) => {
    builder
      //  
      .addCase(loginUser.pending, (state) => {
        state.loginStatus = "loading";
      })
      .addCase(loginUser.fulfilled, (state, action) => {
        state.isLoggedIn = true;
        state.token = action.payload.token;
        state.user = action.payload.user;
        state.error = null;
        state.loginStatus = "succeeded";
      })
      .addCase(loginUser.rejected, (state, action) => {
        state.error = action.payload || action.error.message;
        state.loginStatus = "failed";
      })

      //  
      .addCase(registerUser.pending, (state) => {
        state.registerStatus = "loading";
      })
      .addCase(registerUser.fulfilled, (state) => {
        state.error = null;
        state.registerStatus = "succeeded";
      })
      .addCase(registerUser.rejected, (state, action) => {
        state.error = action.payload || action.error.message;
        state.registerStatus = "failed";
      })

      //    
      .addCase(getCurrentUser.pending, (state) => {
        state.userStatus = "loading";
      })
      .addCase(getCurrentUser.fulfilled, (state, action) => {
        state.isLoggedIn = true;
        state.user = action.payload;
        state.userStatus = "succeeded";
      })
      .addCase(getCurrentUser.rejected, (state, action) => {
        state.error = action.payload || action.error.message;
        state.userStatus = "failed";
      })

      //   (getStatus) 
      .addCase(getStatus.pending, (state) => {
        state.statusState = "loading";
      })
      .addCase(getStatus.fulfilled, (state, action) => {
        state.statusState = "succeeded";
        state.statusData = action.payload;
      })
      .addCase(getStatus.rejected, (state, action) => {
        state.statusState = "failed";
        state.error = action.error.message;
      });
  },
});

//    ID  
export const selectCurrentUserId = (state) => state.auth.user?.id;

//      
export const selectStatusData = (state) => state.auth.statusData;
export const selectStatusState = (state) => state.auth.statusState;
export const selectAuthError = (state) => state.auth.error;

export const { logout } = authSlice.actions;
export default authSlice.reducer;
