// src/components/LoginModal.js
import React, { useState, useEffect, useRef } from "react";
import { useDispatch } from "react-redux";
import { loginUser, registerUser } from "../slices/authSlice";
import "./LoginModal.css";

function LoginModal({ closeModal }) {
  const dispatch = useDispatch();

  //  
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [email, setEmail] = useState("");
  const [isRegistering, setIsRegistering] = useState(false);

  //   
  const [errorMessage, setErrorMessage] = useState("");

  //    
  const [quizQuestion, setQuizQuestion] = useState("");
  const [quizAnswer, setQuizAnswer] = useState("");
  const [userAnswer, setUserAnswer] = useState("");

  const modalRef = useRef(null);

  //   
  const quizzes = [
    {
      question: "      ?",
      answer: "",
    },
    {
      question: "    ?",
      answer: "",
    },
    {
      question: "   ?",
      answer: "",
    },
    {
      question: "    ?",
      answer: "  ",
    },
  ];

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (modalRef.current && !modalRef.current.contains(event.target)) {
        closeModal();
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, [closeModal]);

  //       
  useEffect(() => {
    if (isRegistering) {
      const randomQuiz = quizzes[Math.floor(Math.random() * quizzes.length)];
      setQuizQuestion(randomQuiz.question);
      setQuizAnswer(randomQuiz.answer);
      setUserAnswer(""); //   
      setErrorMessage(""); //   
    } else {
      setErrorMessage(""); //   
    }
  }, [isRegistering]);

  const handleLogin = () => {
    dispatch(loginUser({ username, password }))
      .unwrap()
      .then(() => {
        closeModal();
      })
      .catch((error) => {
        setErrorMessage("    .");
        console.error(" :", error);
      });
  };

  const handleRegister = () => {
    setErrorMessage(""); //   

    if (password !== confirmPassword) {
      setErrorMessage("  .");
      return;
    }

    if (userAnswer.trim() !== quizAnswer.trim()) {
      setErrorMessage(" .");
      return;
    }

    dispatch(
      registerUser({ username, password, email, quizQuestion, userAnswer })
    )
      .unwrap()
      .then(() => {
        alert(" .  .");
        setIsRegistering(false);
      })
      .catch((error) => {
        //     
        if (error && error.message) {
          setErrorMessage(error.message);
        } else {
          setErrorMessage(" .");
        }
        console.error(" :", error);
      });
  };

  return (
    <div className="modal-background">
      <div className="modal-content" ref={modalRef}>
        <h2>{isRegistering ? "" : ""}</h2>
        <input
          type="text"
          placeholder=""
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          className="login-input"
        />
        {isRegistering && (
          <>
            <input
              type="email"
              placeholder=""
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="login-input"
            />
            <input
              type="password"
              placeholder=" "
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              className="login-input"
            />
          </>
        )}
        <input
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className="login-input"
        />
        {isRegistering && (
          <div className="quiz-section">
            <p className="quiz-question">{quizQuestion}</p>
            <input
              type="text"
              placeholder="  "
              value={userAnswer}
              onChange={(e) => setUserAnswer(e.target.value)}
              className="login-input"
            />
          </div>
        )}
        {/*    */}
        {errorMessage && <p className="error-message">{errorMessage}</p>}
        <button
          onClick={isRegistering ? handleRegister : handleLogin}
          className="login-button"
        >
          {isRegistering ? "" : ""}
        </button>
        <button
          onClick={() => setIsRegistering((prev) => !prev)}
          className="toggle-button"
        >
          {isRegistering ? " " : ""}
        </button>
      </div>
    </div>
  );
}

export default LoginModal;
