// src/components/EditPostPage.js

import React, { useState, useEffect, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { fetchPostById, selectPostById, updatePost } from "../slices/postSlice";
import "./UploadPage.css"; // UploadPage  

function EditPostPage() {
  const { postId } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const post = useSelector((state) => selectPostById(state, Number(postId)));
  const isLoggedIn = useSelector((state) => state.auth.isLoggedIn);
  const currentUserId = useSelector((state) => state.auth.user?.id);

  const [title, setTitle] = useState("");
  const [status, setStatus] = useState("PUBLIC");
  const [content, setContent] = useState("");
  const [images, setImages] = useState([]);
  const [imageFiles, setImageFiles] = useState([]);

  //   
  const [tags, setTags] = useState([]);
  const [tagInput, setTagInput] = useState("");

  const editorRef = useRef(null);

  useEffect(() => {
    if (!post) {
      dispatch(fetchPostById(postId));
    } else {
      //  
      if (currentUserId !== post.authorId) {
        alert("  .");
        navigate(`/posts/${postId}`);
        return;
      }

      //     
      setTitle(post.title);
      setStatus(post.status);
      setContent(post.content);
      setTags(post.tags || []);
      setImages(post.imageUrl ? [post.imageUrl] : []);
      //     URL        

      //  
      if (editorRef.current) {
        // Check if Summernote is loaded
        if (window.$ && window.$.fn && window.$.fn.summernote) {
          window.$(editorRef.current).summernote({
            height: 300,
            focus: true,
            lang: "ko-KR",
            placeholder: "   ...",
            toolbar: [
              ["style", ["style"]],
              ["font", ["bold", "italic", "underline"]],
              ["fontsize", ["fontsize"]],
              ["color", ["color"]],
              ["para", ["ul", "ol", "paragraph"]],
              ["table", ["table"]],
              ["insert", ["link", "picture", "video"]],
              ["view", ["fullscreen", "codeview"]],
            ],
            callbacks: {
              onChange: function (contents) {
                setContent(contents);
              },
            },
          });
          //   
          window.$(editorRef.current).summernote("code", post.content);
        } else {
          console.error("Summernote is not loaded.");
        }
      }
    }
  }, [dispatch, post, postId, currentUserId, navigate]);

  //  
  useEffect(() => {
    return () => {
      if (
        editorRef.current &&
        window.$ &&
        window.$.fn &&
        window.$.fn.summernote
      ) {
        window.$(editorRef.current).summernote("destroy");
      }
    };
  }, []);

  const handleImageUpload = (e) => {
    const files = Array.from(e.target.files);
    if (files.length + images.length > 5) {
      alert(" 5  .");
      return;
    }

    const newImages = files.slice(0, 5 - images.length);
    setImages((prevImages) => [
      ...prevImages,
      ...newImages.map((file) => URL.createObjectURL(file)),
    ]);
    setImageFiles((prevFiles) => [...prevFiles, ...newImages]);
  };

  const handleAddTag = () => {
    if (tagInput.trim() && !tags.includes(tagInput.trim())) {
      setTags([...tags, tagInput.trim()]);
      setTagInput("");
    }
  };

  const handleRemoveTag = (tagToRemove) => {
    setTags(tags.filter((tag) => tag !== tagToRemove));
  };

  const handlePostUpdate = async () => {
    if (!isLoggedIn) {
      alert(" .");
      return;
    }

    //   ( useEffect    )
    // if (currentUserId !== post.authorId) {
    //   alert("  .");
    //   return;
    // }

    const formData = new FormData();
    formData.append("title", title);
    formData.append("status", status);
    formData.append("content", content);

    imageFiles.forEach((file) => {
      formData.append("image", file);
    });

    //  
    tags.forEach((tag) => {
      formData.append("tags", tag);
    });

    console.log("Sending tags:", tags); //    

    try {
      // Redux    
      await dispatch(updatePost({ postId, formData })).unwrap();
      alert("  !");
      navigate(`/posts/${postId}`);
    } catch (error) {
      console.error("  .", error);
      alert("  .");
    }
  };

  if (!post) {
    return <p> ...</p>;
  }

  return (
    <div className="upload-page-container">
      <div className="upload-header">
        <h1> </h1>
      </div>

      <div className="content-section">
        <div className="image-upload-section">
          <div className="preview-section">
            {images[0] && (
              <div className="main-preview">
                <img
                  src={images[0]}
                  alt="Main preview"
                  className="main-photo"
                />
              </div>
            )}
            <div className="thumbnail-preview">
              {images.slice(1).map((image, index) => (
                <img
                  key={index}
                  src={image}
                  alt={`Thumbnail ${index + 1}`}
                  className="thumbnail-photo"
                />
              ))}
            </div>
          </div>
          <label htmlFor="image-upload" className="upload-button">
             
          </label>
          <input
            type="file"
            id="image-upload"
            className="image-upload-input"
            onChange={handleImageUpload}
            multiple
            accept="image/*"
          />
        </div>

        <div className="info-section">
          <div className="input-field">
            <label></label>
            <input
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="  "
            />
          </div>

          <div className="input-field">
            <label> </label>
            <select value={status} onChange={(e) => setStatus(e.target.value)}>
              <option value="PUBLIC"></option>
              <option value="PRIVATE"></option>
            </select>
          </div>

          {/*    */}
          <div className="input-field">
            <label></label>
            <div className="tag-input-section">
              <input
                type="text"
                value={tagInput}
                onChange={(e) => setTagInput(e.target.value)}
                placeholder="  Enter "
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    e.preventDefault();
                    handleAddTag();
                  }
                }}
              />
              <button onClick={handleAddTag}></button>
            </div>
            <div className="tag-list">
              {tags.map((tag) => (
                <div key={tag} className="tag-item">
                  {tag}
                  <button onClick={() => handleRemoveTag(tag)}>x</button>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      <div className="content-section">
        <label></label>
        <div ref={editorRef}></div>
      </div>

      <button className="post-upload-button" onClick={handlePostUpdate}>
        
      </button>
    </div>
  );
}

export default EditPostPage;
